<template>
  <div class="qucklyGoods">
    <Add @getData="bannerlist" />
    <Register @getData="bannerlist" :register-visible.sync="registerVisible" :row-data="rowData" />
    <div class="table">
      <el-table
        :data="tableData.list"
        border
        style="width=100%"
        :header-cell-style="{ 'text-align': 'center', color: 'black' }"
        :cell-style="{ 'text-align': 'center' }"
      >
        <el-table-column
          prop="id"
          label="序号"
        />
        <el-table-column label="轮播图">
          <template slot-scope="scope">
            <img
              style="width:50px;height:50px"
              :src="scope.row.image_url"
            >
          </template>
        </el-table-column>
        <el-table-column
          prop="type"
          label="轮播图类型"
        />
        <el-table-column
          prop="position"
          label="轮播图位置"
        />
        <el-table-column
          prop="url"
          label="跳转地址"
        />
        <el-table-column
          prop="status"
          label="显示状态"
        />
        <el-table-column
          prop="sort"
          label="排序"
        />
        <el-table-column
          prop="created_at"
          label="创建时间"
        />
        <el-table-column width="200px">
          <template
            slot="header"
            :slot-scope="scope"
          >
            操作
          </template>
          <template slot-scope="scope">
            <el-button
              size="mini"
              type="primary"
              @click="update(scope.row)"
            >
              修改轮播图
            </el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div class="pagination">
      <el-pagination
        layout="prev,pager,next"
        background
        :page-size="limit"
        :total="tableData.total"
        @current-change="changePage"
        @prev-click="prev"
        @next-click="next"
      />
    </div>
  </div>
</template>

<script>
import Add from "./add.vue";
import Register from "./register.vue";
export default {
    data() {
        return {
            scope:"scope",
            // 编辑框是否显示
            registerVisible:false,
            // 每页条目数
            limit:8,
            // 获取哪一页数据
            pages:1,

            tableData:[],
            // 传给子组件的选中行数据
            rowData:{}

        };
    },
    created() {
        this.bannerlist();
        
    },
    methods: {
        // 普通商品列表
        async bannerlist (){
            try {
                let res = await this.axios.get("/admin/bannerlist",{
                    params:{
                        limit:this.limit,
                        page:this.pages
                    }
                });
                
                this.tableData = Object.assign(res.data, {
                    list: res.data.list.map((item) => {
                        return Object.assign(item, {
                            status: item.status === 1 ? "可用" : "禁用",
                            position: item.position === 1 ? "首页" : "轮播",
                            type:item.type === 1 ? "轮播" : item.type === 2 ? "商品广告" : "视频广告"
                        });
                        
                    }),
                });
            } catch (error) {
                console.log(error);
            }
        },
        // 列表行的编辑
        update(row){
            this.registerVisible = true;
            this.rowData = JSON.parse(JSON.stringify(row));
            this.rowData.status = this.rowData.status === "可用" ? 1 : 0;
            this.rowData.position = this.rowData.position === "首页" ? 1 : 2;
            this.rowData.type = this.rowData.type === "轮播" ? 1 : this.rowData.type === "商品广告" ? 2 : 3;
        },
        // 上一页
        prev() {
            this.pages--;
            this. bannerlist();
        },
        // 翻页
        changePage(page) {
            this.pages = page;
            this. bannerlist();
        },
        // 下一页
        next() {
            this.pages++;
            this. bannerlist();
        }
    },
    components: {
        Add,
        Register
    }
};
</script>

<style lang="scss" scoped>
.qucklyGoods{
    .table{
        margin-top: 15px;
        height:700px;
    }
    .pagination{
        margin-top: 10px;
    }
}
</style>
