<template>
  <div class="goods-container">
    <el-button @click="dialogVisible = true">
      添加广告/轮播
    </el-button>
    <el-dialog
      :visible.sync="dialogVisible"
      width="460px"
      center
      :before-close="close"
    >
      <el-form
        label-width="80px"
      >
        <el-form-item label="轮播地址">
          <el-upload
            style="display:inline"
            action
            :limit="1"
            :http-request="detailImgsUpload"
            :on-remove="removeFile"
            list-type="picture-card"
            ref="uploadTwo"
          >
            <i
              slot="default"
              class="el-icon-plus"
            />
          </el-upload>
        </el-form-item>
        <el-form-item label="跳转地址">
          <div style="width:300px">
            <el-input v-model="url" />
          </div>
        </el-form-item>
        <el-form-item label="显示状态">
          <el-select
            v-model="status"
            clearable
            placeholder="请选择"
            style="width:300px"
          >
            <el-option
              v-for="(item,index) in statusList"
              :key="index"
              :label="item.name"
              :value="item.id"
            />
          </el-select>
        </el-form-item>
        <el-form-item label="轮播类型">
          <el-select
            v-model="type"
            clearable
            placeholder="请选择"
            style="width:300px"
          >
            <el-option
              v-for="(item,index) in typeList"
              :key="index"
              :label="item.name"
              :value="item.id"
            />
          </el-select>
        </el-form-item>
        <el-form-item label="轮播位置">
          <el-select
            v-model="position"
            clearable
            placeholder="请选择"
            style="width:300px"
          >
            <el-option
              v-for="(item,index) in positionList"
              :key="index"
              :label="item.name"
              :value="item.id"
            />
          </el-select>
        </el-form-item>
        <el-form-item label="排序">
          <div style="width:300px">
            <el-input v-model="sort" />
          </div>
        </el-form-item>
      </el-form>
      <span
        slot="footer"
        class="dialog-footer"
      >
        <el-button @click="close">取 消</el-button>
        <el-button
          type="primary"
          @click="confirm"
        >确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { client, setImgName } from "@/config/oss";
export default {
    data() {
        return {
            dialogVisible: false,
            // 广告/轮播地址
            imageUrl:"",
            // 跳转地址
            url:"",
            // 可用 禁用
            statusList:[
                {
                    "id": 1, 
                    "name": "可用"
                }, 
                {
                    "id": 0, 
                    "name": "禁用"
                }
            ],
            status:"",
            // 轮播图类型
            typeList:[
                {
                    "id": 1, 
                    "name": "轮播"
                }, 
                {
                    "id": 2, 
                    "name": "商品广告"
                }, 
                {
                    "id": 3, 
                    "name": "视频广告"
                }
            ],
            type:"",
            // 轮播图位置
            positionList:[
                {
                    "id": 1, 
                    "name": "首页"
                }, 
                {
                    "id": 2, 
                    "name": "抢单"
                }
            ],
            position:"",
            // 排序
            sort:""
        };
    },
    // computed: {
    //   // 判断选择了几张，一张时隐藏选择
    //   uploadDisabled:function() {
    //       return this.image_url.length >0
    //   },
    // },
    methods: {
        // 详情图片上传
        async detailImgsUpload (file){
            let url =  await setImgName( client(),file);
            this.imageUrl = url;
        },
        // 删除图片时 清空存在的值
        removeFile(){
            this.imageUrl = "";
        },
        // 添加账号
        async confirm (){
            if(this.image_url === ""){
                this.$message({
                    message: "请选择广告/轮播地址",
                    type: "warning"
                });
                return;
            }
            // if(this.url === ""){
            //     this.$message({
            //         message: "请填写跳转地址",
            //         type: "warning"
            //     });
            //     return;
            // }
            if(this.status === ""){
                this.$message({
                    message: "请填写显示状态",
                    type: "warning"
                });
                return;
            }
            
            if(this.type === ""){
                this.$message({
                    message: "请填写轮播图类型",
                    type: "warning"
                });
                return;
            }
            if(this.position === ""){
                this.$message({
                    message: "请填写轮播图位置",
                    type: "warning"
                });
                return;
            }
            if(this.sort === ""){
                this.$message({
                    message: "请填写排序",
                    type: "warning"
                });
                return;
            }
            try {
                let res = await this.axios.post("admin/addbanner",{
                    "image_url":this.imageUrl, // 广告/轮播地址
                    "url": this.url, // 跳转地址
                    "status":this.status, // 可用 禁用
                    "type":this.type, // 轮播图类型
                    "position":this.position, // 轮播图位置
                    "sort":this.sort // 排序
                });
                
                this.close();
                this.$emit("getData");
                this.$message({
                    message:res.msg,
                    type:"success"
                });
            } catch (error) {
                console.log(error);
            }
        },
        close (){ 
            // 添加成功后，情况表单的内容
            this.imageUrl = "";
            this.url = "";
            this.status = "";
            this.type = "";
            this.position = "";
            this.sort = "";
            this.dialogVisible = false;
            this.$refs["uploadTwo"].clearFiles();
        }
    },
    components: {

    }
};
</script>

<style lang="scss" scoped>
.goods-container{
  .size{
    display: flex;
    justify-content: space-between;
    width: 450px;
    margin-bottom: 10px;
    .size-item{
      width: 270px;
    }
  }
}
</style>
