/* eslint-disable camelcase */
/* eslint-disable camelcase */
<template>
  <div class="goods-container">
    <el-dialog
      :visible.sync="registerVisible"
      width="460px"
      center
      :before-close="close"
    >
      <el-form
        label-width="80px"
      >
        <el-form-item label="轮播地址" style="height:148px;overflow: hidden;">
          <el-upload
            style="display:inline"
            action
            :limit="1"
            :http-request="detailImgsUpload"
            :on-remove="removeFile"
            :before-upload="beforeUpload"
            :on-exceed="onExceed"
            list-type="picture-card"
            ref="uploadTwo"
          >
            <i
              slot="default"
              class="el-icon-plus"
            />
          </el-upload>
          <transition name="el-fade-in-linear">
            <img
              style="height:148px;width:148px"
              :src="rowData.image_url"
              v-show="desImgShow"
            >
          </transition>
        </el-form-item>
        <el-form-item label="跳转地址">
          <div style="width:300px">
            <el-input v-model="rowData.url" />
          </div>
        </el-form-item>
        <el-form-item label="显示状态">
          <el-select
            v-model="rowData.status"
            clearable
            placeholder="请选择"
            style="width:300px"
          >
            <el-option
              v-for="(item,index) in statusList"
              :key="index"
              :label="item.name"
              :value="item.id"
            />
          </el-select>
        </el-form-item>
        <el-form-item label="轮播类型">
          <el-select
            v-model="rowData.type"
            clearable
            placeholder="请选择"
            style="width:300px"
          >
            <el-option
              v-for="(item,index) in typeList"
              :key="index"
              :label="item.name"
              :value="item.id"
            />
          </el-select>
        </el-form-item>
        <el-form-item label="轮播位置">
          <el-select
            v-model="rowData.position"
            clearable
            placeholder="请选择"
            style="width:300px"
          >
            <el-option
              v-for="(item,index) in positionList"
              :key="index"
              :label="item.name"
              :value="item.id"
            />
          </el-select>
        </el-form-item>
        <el-form-item label="排序">
          <div style="width:300px">
            <el-input v-model="rowData.sort" />
          </div>
        </el-form-item>
      </el-form>
      <span
        slot="footer"
        class="dialog-footer"
      >
        <el-button @click="close">取 消</el-button>
        <el-button
          type="primary"
          @click="confirm"
        >确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { client, setImgName } from "@/config/oss";
export default {
    data() {
        return {
            dialogVisible: false,
            // 默认显示的图片
            desImgShow:true,
            // 广告/轮播地址
            // image_url:"",
            // 跳转地址
            // url:"",
            // 可用 禁用
            statusList:[
                {
                    "id": 1, 
                    "name": "可用"
                }, 
                {
                    "id": 0, 
                    "name": "禁用"
                }
            ],
            // status:"",
            // 轮播图类型
            typeList:[
                {
                    "id": 1, 
                    "name": "轮播"
                }, 
                {
                    "id": 2, 
                    "name": "商品广告"
                }, 
                {
                    "id": 3, 
                    "name": "视频广告"
                }
            ],
            // type:"",
            // 轮播图位置
            positionList:[
                {
                    "id": 1, 
                    "name": "首页"
                }, 
                {
                    "id": 2, 
                    "name": "抢单"
                }
            ],
            // position:"",
            // 排序
            // sort:""
        };
    },
    created() {
        // this.image_url=this.rowData.image_url;
    },
    props:{
        registerVisible:{
            type:Boolean,
            defautl:false,
        },
        rowData:{
            type:Object,
            default (){
                return {};
            }
        }
    }, 
    methods: {
        // 详情图片上传
        async detailImgsUpload (file){
            let url =  await setImgName( client(),file);
            this.rowData.imageUrl = url;
        },
        // 删除图片时 清空存在的值
        removeFile(){
            this.rowData.imageUrl = "";
        },
        // 上传文件之前的隐藏 默认显示的图片
        beforeUpload(){
            this.desImgShow = false;
        },
        // 文件超出个数限制时
        onExceed(){
            this.$message({
                message: "只能选择一张图",
                type: "warning"
            });
        },
        // 添加账号
        async confirm (){
            if(!this.rowData.image_url){
                this.$message({
                    message: "请选择广告/轮播地址",
                    type: "warning"
                });
                return;
            }
            // if(this.url==""){
            //     this.$message({
            //       message: '请填写跳转地址',
            //       type: 'warning'
            //     });
            //     return;
            // }
            // if(this.status==""){
            //     this.$message({
            //       message: '请填写显示状态',
            //       type: 'warning'
            //     });
            //     return;
            // }
            
            // if(this.type==""){
            //     this.$message({
            //       message: '请填写轮播图类型',
            //       type: 'warning'
            //     });
            //     return;
            // }
            // if(this.position==""){
            //     this.$message({
            //       message: '请填写轮播图位置',
            //       type: 'warning'
            //     });
            //     return;
            // }
            // if(this.sort==""){
            //     this.$message({
            //       message: '请填写排序',
            //       type: 'warning'
            //     });
            //     return;
            // }
            try {
                let res = await this.axios.post("admin/savebanner",{
                    "id":this.rowData.id, // 主键ID
                    "image_url":this.rowData.image_url, // 广告/轮播地址
                    "url": this.rowData.url, // 跳转地址
                    "status":this.rowData.status, // 可用 禁用
                    "type":this.rowData.type, // 轮播图类型
                    "position":this.rowData.position, // 轮播图位置
                    "sort":this.rowData.sort // 排序
                });
                
                this.close();
                this.$emit("getData");
                this.$message({
                    message:res.msg,
                    type:"success"
                });
            } catch (error) {
                console.log(error);
            }
        },
        close (){ 
            // 添加成功后，情况表单的内容
            this.imageUrl = "";
            this.url = "";
            this.status = "";
            this.type = "";
            this.position = "";
            this.sort = "";
            this.dialogVisible = false;
            this.desImgShow = true;
            this.$refs["uploadTwo"].clearFiles();
            this.$emit("update:registerVisible", false);
        }
    },
    components: {

    }
};
</script>

<style lang="scss" scoped>
.goods-container{
  .size{
    display: flex;
    justify-content: space-between;
    width: 450px;
    margin-bottom: 10px;
    .size-item{
      width: 270px;
    }
  }
}
</style>